import React from "react";
import {StaticImage} from "gatsby-plugin-image"


const Banner = () => {
  return (
    <section className="section-hero pb-5">
      <div className="container">
        <div className="row">
          <div className="col-md-7 pe-5 col-sm-12">
            <div className="hero-text-box">
              <h1 className="animate__animated animate__zoomIn" id="welcome">
                Welcome to Justtry
              </h1>
              <p className="animate__animated animate__fadeInUpBig text-white">
                Our team of experienced web developers is dedicated to
                delivering high-quality, user-friendly websites that exceed our
                client's expectations.
              </p>
              <p className="animate__animated animate__fadeInUpBig text-white">
                Contact us today to see how we can help bring your vision to
                life
              </p>

              <p className="animate__animated animate__fadeInUpBig text-white">
                Let Justtry take care of the product and processes so you can
                focus on business development
              </p>

              <a
                href="#cta"
                className=" animate__animated animate__fadeInUpBig custom-btn btn--full margin-right-sm"
              >
                Get Started
              </a>
              <a
                href="#how"
                className="animate__animated animate__fadeInUpBig custom-btn btn--outline"
              >
                Learn more &darr;
              </a>
            </div>
          </div>
          <div className="col-md-5 col-sm-12">
            <div className="hero-img-box">
              <StaticImage
                className="bottomAnimation hero-img"
                src="../../styles/assets/images/ServiceLogo/Computer-removebg-preview.png"
                alt="Banner hero design image"
              />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Banner;
